<template>
    <section class="main-body">
    <div class="row">
        
        <side-nav @emitClick="sideNavClick($event, i)" :currentView = "currentView"></side-nav>
        <main-content @clickToContact="clickToContact" :currentView="currentView" :currentContent="currentContent"></main-content>
        <blue-quote :currentView="currentView" :tempIndex="currentContent"></blue-quote>
        
    </div>
    
    <div class="row body-sides">
    <side-form></side-form>
    
    <div class="main-logo col-xs-12 col-md-4 pull-right" style="background-image: url(./static/harris-law-logo-min7.png);">
        
    </div>
    </div>
    
    </section>
</template>

<script>

    import SideNav from './Side-nav.vue'
    import MainContent from './Main-content.vue'
    import BlueQuote from './Blue-quote.vue'
    import SideForm from './Side-form.vue'
    
    export default {
        props: ['currentView'],
        data () {
            return {
                currentContent: '1',
                i: ''
            }
        },
        methods: {
            sideNavClick: function(i) {
                this.currentContent = ( i + 1 );
                console.log('side nav click processed');
    },
            clickToContact: function() {
                this.$emit('clickToContact');
            }
    },
        components: {
            'side-nav': SideNav,
            'main-content': MainContent,
            'blue-quote': BlueQuote,
            'side-form': SideForm
        },
       watch: { 
      	     currentView: function() { // watch for change in currentView 
                 this.currentContent = 1;
            }
    }
    }
</script>

<style >
    
    section.main-body{
        z-index: 15;
    position: relative;
    }
    
    .one, .two, .three, .four{
    height: 700px;
    }
    
    .main-logo{
    background-size: 260px;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    min-height: 483px;
    background-color: #e7e7e7;
    }
    
    h3{
        font: 400 20px '77995aba';
    font-family: 'Yeseva One', cursive;
    }
    
    @media (max-width: 991px){
    h3{
        font: 400 48px '77995aba';
    }
        
        .main-logo{
    background-size: 380px;
        }
    }

</style>


