<template>
  <div id="app">
   <lines class="cover-lines" v-if=" view == 'cover'"></lines>
   <lines class="main-lines" v-if=" view == 'main' || view == 'both' "></lines>
   <cover-view @coverToNavBottom="coverToNavBottom" @viewChange="viewTrans(permitNavTween)" v-if="view == 'cover' || view == 'both'"></cover-view>
    <main-view @permitTween="permitNavTween" :coverToNavPrevTween="coverToNavPrevTween" :viewChanges="viewChanges" @coverToNavBottom="coverToNavBottom" :coverSelectView="coverSelectView"></main-view>
    <bottom @bottomUpComplete="triggerViewTransition()" ref="bottom"></bottom>
    </div>
</template>

<script>
import Lines from './lines.vue'
import MainView from './Main-view.vue'
import CoverView from './Cover-view.vue'
import Bottom from './Bottom.vue'
    
export default {
    components: {
      'main-view': MainView,
      'cover-view': CoverView,
        'bottom': Bottom,
        'lines': Lines
    },
  data () {
    return {
        // have cover, main, and both? forwhen we need to hide visibility of cover view and show cover-items?
        view: 'cover',
        coverSelectView: 1,
        viewChanges: 0,
        coverToNavPrevTween: "prevent"
    }
  },
    methods: {
        viewTrans: function(permitNavTween) {
            
            if ($('.cover-item.bg-primary').hasClass('kurt') ){
                this.view = 'both';
                this.coverSelectView = 1;
            } else if ($('.cover-item.bg-primary').hasClass('practice') ){
                this.view = 'both';
                this.coverSelectView = 2;
            } else if ($('.cover-item.bg-primary').hasClass('consultation') ){
                this.view = 'both';
                this.coverSelectView = 3;
            } else if ($('.cover-item.bg-primary').hasClass('contact') ){
                this.view = 'both';
                this.coverSelectView = 4;
            }
            
            // make cover nav except nav items visibility hidden()
            
            $('.cover-view .cover-item').addClass('reshow');
            $('.cover-view').addClass('hide');

            $('#main-nav, .side-nav').css('display', 'block');
            $('.view-title').css('display', 'block');
            $('#main-view').css('display', 'block');
            console.log(this.coverSelectView);
            
            // must be called after and seperately, post update()
        },
        permitNavTween:function(){
            // oncomplete invoke initialViewChange() n pass prop down to prevent tweenNavItems() on cover to main switch
            this.coverToNavPrevTween = "permit";
            //this.view = 'main';
            console.log('nav tween permitted!');
            // create its own method, call on comlpete
        },
        coverToNavBottom: function() {
                this.$refs.bottom.bottomUp();
            
            //oncomplete pass change to main-view prop
            },
        triggerViewTransition: function(){
            this.viewChanges++;
            console.log("view changes = " + this.viewChanges);
        },
        
        hideCoverItems: function(){
            this.view = 'main';
        },
        revealLines: function(){
                $(".main-lines .line").delay(2300).fadeIn('slow');
            },
        processWidth: function(){
            
            if ($(window).width() < 992) {
                this.view = 'both';
                this.coverSelectView = 1;
                
                $('.cover-view .cover-item').addClass('reshow');
            $('.cover-view').addClass('hide');

            $('#main-nav, .side-nav').css('display', 'block');
            $('.view-title').css('display', 'block');
            $('#main-view').css('display', 'block');
                
            } else {
                $('.bottom').addClass('wide-push');
            }
        },
        processURL: function() {
            if (self.document.location.hash == '#/kurt-harris/'){
    this.kurtHarris();
    
        }
        
        else if (self.document.location.hash == '#/practice/'){
    this.practice();
    
        } else if (self.document.location.hash == '#/consultation/'){
    this.consultation();
    
        }
            else if (self.document.location.hash == '#/contact/'){
    this.contact();
    
        } else{
            // do nothing
        }
        },
        kurtHarris: function(){
            this.view = 'main';
            this.coverSelectView = 1; 
            $('#main-nav, .side-nav').css('display', 'block');
            $('.view-title').css('display', 'block');
            $('#main-view').css('display', 'block');
            this.triggerViewTransition();
            this.$refs.bottom.emitUpComplete();
            
            console.log('on kurt harris page!');
        },
        practice: function(){
            this.view = 'main';
            this.coverSelectView = 2;
            $('#main-nav, .side-nav').css('display', 'block');
            $('.view-title').css('display', 'block');
            $('#main-view').css('display', 'block');
            this.triggerViewTransition();
            this.$refs.bottom.emitUpComplete();
            
            console.log('on practice page!');
        },
        consultation: function(){
            this.view = 'main';
            this.coverSelectView = 3;
            $('#main-nav, .side-nav').css('display', 'block');
            $('.view-title').css('display', 'block');
            $('#main-view').css('display', 'block');
            this.triggerViewTransition();
            this.$refs.bottom.emitUpComplete();
            
            console.log('on consultation page!');
        },
        contact: function(){
            this.view = 'main';
            this.coverSelectView = 4;
            $('#main-nav, .side-nav').css('display', 'block');
            $('.view-title').css('display', 'block');
            $('#main-view').css('display', 'block');
            this.triggerViewTransition();
            this.$refs.bottom.emitUpComplete();
            
            console.log('on contact page!');
        }
    },
    watch: {
        coverToNavPrevTween: function(newVal, oldVal){
            if ( newVal == 'permit' ){
                
                setTimeout(this.hideCoverItems, 1700);
                
                this.revealLines();
                
            }
        }
           },
    mounted: function(){
        this.processWidth();
        this.processURL();
        
            }
        }
    
    
</script>

<style>
    
    
    
    body{
        font-size: 17px;
        color: #555;
    }
    
    .row{
        margin-left: 0;
        margin-right: 0;
    }
    
    .hide{
        visibility: hidden;
    }
    
    .reshow{
        visibility: visible;
    }
    
    #main-view{
        display: none;
    }
    
    #main-nav, .side-nav{
        display: none;
    }
    
    .btn-primary{
    color: #fff;
    background-color: #0f88ad;
    border-color: #0f88ad;
    border-radius: 0;
    }
    
    .bg-primary{
    color: #fff !important;
    background-color: #0f88ad !important;
    }
    
    .bg-secondary{
        color: #555;
    background-color: #fff;
    }
    
    .btn-default{
    color: #555;
    background-color: #fff;
    border-color: #ccc;
    }
    
    .btn-primary:hover {
        background-color: #1a97bd;
        border-color: #1a97bd;
    }
    
    a{
        color: #0f88ad ;
    }
    
    a:hover{
        color: #1a97bd ;
        cursor: pointer;
    }
    
    .main-logo, .blue-quote{
        z-index: 9999;
    }
    
    @media (max-width: 1800px){
        body, .blue-quote p{
            font-size: 15px !important;
            line-height: 22px !important;
        }
        
        .view-title{
            font: 700 18px "77995aba";
        }
}
    
    @media (max-width: 991px){
        #bottom{
            top: auto;
            }
        
        .cover-lines, .main-lines{
        display: none !important;
        }
        
        body, .blue-quote p{
     font-size: 42px !important;
    line-height: 58px !important;
        }
        
    }
    
    
    
</style>





