
   <template>

   <div>
   <ul class="nav-items">
    <li class="col-md-3"   v-for="(title, i) in NavItems"  v-bind:class="{active: i + 1 == currentView, inactive:i + 1 != currentView}" @click="emitClick(i)"><span class="view-title"><i @click="emitPrevView" class="fa fa-arrow-left pull-left"></i>{{ title }}<i @click="emitNextView" class="fa fa-arrow-right pull-right"></i></span></li>
    </ul>

    </div>
</template>
<script>
    window.$ = window.jQuery = require('jquery');
</script>
<script>
    
    export default{
        updated(){
            this.tweenNavItems();
        },
        props: ['currentView', 'coverToNavPrevTween'],
        
        data(){
            return{
                NavItems: [
                     'KURT HARRIS',
                     'PRACTICE AREAS',
                     'CONSULTATION',
                     'CONTACT' 
                ]
            }
        },
        computed: {
            
        },
        methods: {
            emitClick: function(i){
                if (i + 1 == this.currentView){
                    // do nothing
                } else{
                this.$emit('emitClick', i)
                }
            },
            
            emitNextView: function(){
                this.$emit('emitNextView');
            },
            emitPrevView: function(){
                this.$emit('emitPrevView');
            },
            
            tweenNavItems: function(){
                
                if ( this.coverToNavPrevTween == "prevent" ){
                    
                    if ($(window).width() > 991) {
                    //tween active down
                    
                TweenLite.to($('.active'), 0.01, {
                top: "315px",
                    ease: Power3.easeOut
            });
                
            //tween last back up
                 TweenLite.to($('.inactive'), 0.01, {
                top: "0px",
                ease: Power3.easeOut
            });
                    this.$emit('permitTween');
                    } else {
                        this.$emit('permitTween');
                    }
                    
                } else {
                
                    if ($(window).width() > 991) {
                    
            //tween active down
                TweenLite.to($('.active'), 1.2, {
                top: "315px",
                ease: Power3.easeOut
            });
                
            //tween last back up
                 TweenLite.to($('.inactive'), 1.2, {
                top: "0px",
                ease: Power3.easeOut
            });
                        
                    } else{
                        // do nothing
                    }
                    
                }
               
                //^ good for cover to main transition... must add animation duration for main view trans
                
            }
        }
    }
    
</script>

<style>
    
    .nav-items{
        margin-top: -10px;
        margin-left: -40px;
        z-index: 26;
        position: relative;
    }
    
    .view-title{
        display: none;
        font: bold 18px '77995aba';
        font-family: 'Inknut+Antiqua', serif;
    }
    
    .inactive{
        color: #555;
        background-color: #fff;
    }
    
    .inactive:hover{
        color: #fff;
        background-color: #0f88ad;
        top: 0;
    }
    
    .active{
        color: #fff;
        background-color: #0f88ad;
        top: 315px;
    }
    
    li{
        list-style: none;
        z-index: 80;
        cursor: pointer;
        text-align: center;
        padding: 15px;
        font-weight: 400;
        font-size: 17px;
    }
    
    
    li.inactive .fa{
        display: none;
    }
    
    li:first-child .fa-arrow-left{
        display: none;
    }
    
    li:nth-child(4) .fa-arrow-right{
        display: none;
    }
    
    @media (max-width: 991px){
        li .fa-arrow-left, li .fa-arrow-right{
            display: none;
        }
        
        .nav-items{
            margin-top: 0;
            margin-bottom: 0;
        }
        
        .active{
        top: auto;
        }
        
        .view-title{
        font: bold 48px '77995aba';
        }
        
        li{
            padding: 25px;
        }
        
    }

</style>


