
 <template>
  
   <nav id="main-nav">
   <div class="container-fluid">
   <nav-item @permitTween="permitTween" :coverToNavPrevTween="coverToNavPrevTween" @emitPrevView="emitPrevView" @emitNextView="emitNextView" @emitClick="emitClick($event, i)" :currentView="currentView"></nav-item>
   <nav-img :currentView="currentView"></nav-img>
    </div>
    </nav>
</template>

<script>
    import NavItem from './Nav-item.vue'
    import NavImg from './Nav-img.vue'
    
    export default{
        
        props: ['currentView', 'coverToNavPrevTween'],
        data(){
            return{
                message: 'hellur',
                i: '' //just prevent error
            }
        },
        components: {
            'nav-item': NavItem,
            'nav-img': NavImg
        },
        methods: {
            changeView: function(x) {
                //change img()
                
            },
            emitClick: function(i){
                this.$emit('emitClick', i)
            },
            emitPrevView: function(){
                this.$emit('emitPrevView');
            },
            emitNextView: function (){
                this.$emit('emitNextView');
            },
            permitTween: function(){
                this.$emit('permitTween');
            }
        }
    }
</script>

<style>

.container-fluid {
    padding-left: 0;
    padding-right: 0;
}
    
    #nav{
        z-index: 5;
    }
    
</style>



