<template>
    <div class="cover-view">
    
    <overlay></overlay>
    
    <div class="row" style="z-index:26;display:absolute;width:100%;top:0;">
       
       <div @click.prevent="coverToNav" @mouseover="coverItemHover" @mouseleave="coverItemHoverEnd" class="cover-item practice col-md-3 pull-right text-center col-md-offset-3" style="margin-top: 615px; top: 75px;position:absolute;border-right:1px solid rgba(0, 0, 0, 0.15);">
                <h3 style="padding-bottom: 20px;">PRACTICE AREAS</h3>
                <span class="plus">+</span>
                <p style="display: block;">Natus error sit voluptatem accusantium doloremque laudantium.</p>
            </div>
            
            <div @click.prevent="coverToNav" @mouseover="coverItemHover" @mouseleave="coverItemHoverEnd" class="cover-item consultation col-md-3 pull-right text-center col-md-offset-6" style="margin-top: 615px; top: 75px;position:absolute;">
                <h3 style="padding-bottom: 20px;">CONSULTATION</h3>
                <span class="plus">+</span>
                <p>Natus error sit voluptatem accusantium doloremque laudantium.</p>
            </div>
            
        <img class="men-waiting col-md-9" src="assets/harris-cover-b4c4ca-softlight-flipped.jpg" style="padding-left: 0;padding-right:0;object-fit:cover;height: 700px;">
        
        <div @click.prevent="coverToNav" @mouseover="coverItemHover" @mouseleave="coverItemHoverEnd" class="cover-item contact col-md-3 pull-right text-center" style="height: 350px; right: 0;position:absolute;">
            <h3 style="padding-bottom: 20px;">CONTACT</h3>
            <span class="plus">+</span>
            <p>Natus error sit voluptatem accusantium doloremque laudantium.</p>
        </div>
        
        <div class="cover-established col-md-3 pull-right" style="background-color: #0F88AD;height: 350px;background-image: url(assets/harris-established2.jpg);background-position: center;top:350px;"></div>
        
        
        <div class="row" style="background-color: #efefef;">

            <div @click.prevent="coverToNav" @mouseover="coverItemHover" @mouseleave="coverItemHoverEnd" class="cover-item kurt col-md-3 pull-left text-center" style="top: 1040px;position: absolute;">
                <h3 style="padding-bottom: 20px;">KURT HARRIS</h3>
                <span class="plus">+</span>
                <p>Natus error sit voluptatem accusantium doloremque laudantium.</p>
            </div>
            
            <div class="cover2 col-md-3 pull-left text-center col-md-offset-3 bg-primary" style="height: 350px; z-index: 80;padding: 60px 70px;top: 1040px;position: absolute;">
                <h3 style="color: #fff;padding-bottom: 20px;font-size:3em;">A FIRM<br>YOU CAN<br><span style="font-size:1.4em;">TRUST</span></h3>
            </div>

            <img class="col-md-6 pull-right" style="padding: 0;height: 700px;object-fit: cover;" src="assets/legal9.jpg">

        </div>
        
    </div>
    
    
    </div>
</template>

<script>
    
    import Overlay from './Overlay.vue'
    
    export default {
        props: ['view'],
        data () {
            return {
            }
        },
        components: {
            'overlay': Overlay,
        },
        methods: {
            
            coverItemHover: function(event) {
                var item = event.target;
                
                $("h3", item).addClass('h3-hover');

                TweenMax.to($("p", item), 0.9, {
                             opacity: 0,
                             ease: Power2.easeOut,
                             delay: 0
                         });
                
                TweenMax.to($('span.plus', item), 0.9, {
                             opacity: 1,
                             ease: Power2.easeOut,
                             delay: 0
                         });
            },
            
            coverItemHoverEnd: function(event) {
                var item = event.target;
                $("h3", item).removeClass('h3-hover');
                
                TweenMax.to($("p", item), 1, {
                             opacity: 1,
                             ease: Power2.easeOut,
                             delay: 0
                         });
                
                TweenMax.to($('span.plus', item), 0.9, {
                             opacity: 0,
                             ease: Power2.easeOut,
                             delay: 0
                         });
            },
            
            coverToNav: function(event){
                $("html, body").animate({
        scrollTop: 0
    }, "slow");
                
             /*   TweenLite.to($("img.men-waiting"), 2.2, {
            height: "300px",
            top:"-800px",
            delay: 0.6,
            ease: Power1.easeInOut
        });
           */
                $('div.cover2, div.cover-established').fadeOut('slow');
                
                var target = event.target;
                $(target).delay(10000).addClass('bg-primary');
                
                TweenLite.to($(".cover-item"), 1.1, {
        height: "50px",
        padding: 0,
        top: "0px",
        marginTop: 0,
        delay: 0.3,
        position: 'absolute',
        ease: Power1.easeInOut
    });
                
                TweenLite.to($(".cover-item.bg-primary"), 0.8, {
        height: "50px",
        padding: 0,
        top: "315px",
        marginTop: 0,
        delay: 0.3,
        position: 'absolute',
        ease: Power1.easeInOut,
        onComplete: this.removeHover
    });
    
   // $('.overlay').delay(1000).css('display', 'none');
             
                //$('.cover-item p, .cover-item h3').delay(1000).fadeOut(300);
    
                this.$emit('coverToNavBottom');
                //emit this ^
                
            },
            
            removeHover: function(){
                
                $(".cover-item").addClass('no-hover');
                
                this.$emit('viewChange');
                
                // emit event for main-view to display --- include click event
            }

        }
    }
    
</script>

<style scoped>
    
    .cover-view{
        background-color: #fff;
        width: 100%;
        height: 800px;
        position: absolute;
        top: 0;
    }
    
    .overlay{
        z-index: 9999999;
        background-color: #e7e7e7;
        z-index: 10;
        position: relative;
    }
    
    .men-waiting{
        background-color:#6a6a6a;
        position: relative;
        z-index: 5;
    }
    
    h3{
    color: #0f88ad ;
    margin-bottom: 0;
    font: bold 20px '77995aba';
    font-family: 'Inknut+Antiqua', serif;
    }
    
    span.plus{
        display: block;
        left: 47%;
        position: absolute;
        opacity: 0;
        margin-top: 2.2rem;
        font-size: 1.5em;
        font-weight: 100;
    }
    
    .cover-item{
        background-color: #fff;
        height: 350px;
        z-index: 20;
        padding: 110px 70px;
        position: absolute;
    }
    
    .cover-item h3, .cover-item p{
        pointer-events: none;
    }
    
    .cover-item:hover {
     cursor: pointer;   
    }
    
    .h3-hover{
        padding-top: 20px;
        border-top: 1px solid #0f88ad;
        border-bottom: 1px solid #0f88ad;
    }
    
    .bg-primary h3{
        color: #fff !important;
        background-color: #0f88ad !important;
    }
    
    .no-hover h3{
        padding-top: 0 !important;
        border-top: 0 !important;
        border-bottom: 0 !important;
    }
    
    
</style>


