<template>
    <div class="bottom" >
      
      <div class="lines-wrap">
       <div class="line line-1"></div>
       <div class="line line-2"></div>
       <div class="line line-3"></div>
   </div>
       
       <section id="contact-cta" class="contact-cta bg-secondary text-center">
            <div class="container">
                <h3>Have questions?</h3>
                <h2>We have legal experts ready to help.</h2><br>
                <a href="#/contact/" onClick="reloadPage()"><button type="button" class="btn btn-lg btn-primary">Get in Touch</button></a>
            </div>
        </section>
        
        <div id="map"></div>
        
    </div>
</template>

<script>
    export default {

        data () {
            return {
            }
        },
        components: {
        },
        methods: {
            emitUpComplete: function() {
                
                if ($(window).width() > 991) {
                    
                this.$emit('bottomUpComplete');
                TweenLite.to($('.bottom'), 1.3, {
                top: "1190px",
                delay: 1.5,
                ease: Power3.easeInOut
                    // emit event to parent, then call currentView change??
            });
                
            }   else {
                    this.$emit('bottomUpComplete');
                }
                
            },
            bottomUp: function() {
                
                if ($(window).width() > 991) {
                
                TweenLite.to($('.bottom'), 1.2, {
                top: "340px",
                    delay: 0,
                ease: Power3.easeInOut,
                onComplete: this.emitUpComplete
                    // emit event to parent, then call currentView change??
            });
                //$(".cover-lines .line").fadeOut('slow');
            }   else{
                this.emitUpComplete();
            }
                
            }
        }
    }
</script>

<style scoped="true">
    
    .bottom{
        position: absolute;
        min-height: 900px;
        width: 100%;
        z-index: 17;
    }
    
    .wide-push{
        top: 1800px;
    }
    
    .bottom h3{
    color: #0f88ad;
    margin-bottom: 0;
    }
    
    .contact-cta .container {
        padding: 100px;
    }
    
    .contact-cta h2{
        font-size: 24px;
    }
    
    #map{
        height: 600px;
    width: 100%;
    }
    
     .bottom .line{
        position: absolute;
        width: 1px;
        height: 40px;
        top: 0;
        margin-left: -1px;
        background: rgba(0,0,0,0.15);
        z-index: 17;
        /* switch between z-index 15 n 20 */
    }
    
    .bottom .line.line-1{
        left: 25%;
        height: 60px;
    }
    
    .bottom .line.line-2{
        left: 50%;
        height: 0px;
    }
    
    .bottom .line.line-3{
        left: 75%;
        height: 60px;
    }
    
    @media (max-width: 991px){
        .contact-cta h2{
            font-size: 42px !important;
        }
    }
</style>


