<template>
    <div class="main-content col-xs-12 col-md-6 col-lg-5 ">

       <div v-if="currentView == 1" class="content-module first col-sm-12">
        <h3 >{{ content.first[contentIndex - 1].header}}</h3>
        <hr class="hr-blue">
        
        <!-- <div v-if="currentView == 1 && currentContent == 1" class="content-module" style="margin-bottom:20px;margin-top:40px;">
        <img src="assets/Kurt-office16.jpg" alt="" class="img-responsive serving-clients">
    </div> -->
        
        <div v-html="content.first[contentIndex - 1].paragraphs"></div>
        </div>
        
        <div v-else-if="currentView == 2" class="content-module second col-sm-12">
        <h3 >{{ content.second[contentIndex - 1].header}}</h3>
        <hr class="hr-blue">
        <div v-html="content.second[contentIndex - 1].paragraphs"></div>
        </div>
        
        <div v-else-if="currentView == 3" class="content-module third col-sm-12">
