        <div v-html="content.fourth[contentIndex - 1].paragraphs"></div>
        
        <contact-form></contact-form>
        
        </div>

    </div>
</template>

<script>
    
    import ContactForm from './Contact-form.vue'
    
    export default {
        props: ['currentView', 'currentContent'],
        watch: {
            currentContent: function(){
                this.contentTransition();
            }
        },
        methods: {
                    contentTransition: function(){
                      TweenMax.to($(".content-module"), 0.6, {
                             left: -800,
                             ease: Power2.easeOut,
                             delay: 0,
                            onComplete: this.updateContentIndex
                         });

                    },
                    updateContentIndex: function (){
                        $(".content-module").css({left: 600});

                        this.contentIndex = this.currentContent
                        
                        TweenMax.to($(".content-module"), 0.6, {
                             left: 0,
                             ease: Power2.easeOut,
                             delay: 0
                         });
                    },
                    clickToContact: function (){
                        this.$emit('clickToContact');
                    },
                
            },
               /* watch: {
                    currentContent: function() { // watch for change in currentContent
                 this.contentTransition();
            }
                },*/
        data () {
            return {
                
                contentIndex:  '1',
                content: {
                    first: [
                        { header: 'Kurt Harris, Esq., P.C.',
                        paragraphs: '<p>For over 300 trials, mediations and arbitrations Kurt Harris, Esq. has exemplified sound and trustworthy legal advice to his clients in California, Nevada, Idaho, Utah, Washington and Washington D.C.. His extensive background in estate and trust preservation, foreclosure, litigation and asset protection makes him the ideal attorney to assist you in protecting the assets you have worked so hard to earn.</p><h3>SPECIALTIES</h3><br><ul><li>Estate Planning</li><li>Litigation</li><li>Wireless Industry Consulting</li><li>Jury/Bench Trials</li><li>Personal Injury</li><li>Telecommunications Industry Consulting</li><li>Foreclosure Mediating and Arbitration</li>'}],
                    
                    second: [
                        { header: 'A FIRM YOU CAN TRUST WITH ANY FAMILY LEGAL MATTER',
                        paragraphs: '<p>Harris Law Office is dedicated to delivering the highest level of client satisfaction with family or domestic legal matters. We specialize in child custody, divorce, child support, and mediation.</p><p>Because family law is complex, case presentation will directly affect the outcome. Thats why you need to find a law firm you can trust.</p><p>Family law cases are often emotionally charged, and create frustration or despair on what your future may hold. We understand this time in your life may be stressful, thats why we want to be your trusted representation and strong, knowledgeable counsel.</p><ul><li>Divorce</li><li>Child Custody</li><li>Child Support</li><li>Alimony</li><li>Visitation</li><li>Family Law Mediation</li><li>Paternity</li><li>Domestic Violence</li><li>Adoption and Guardianship</li><li>Pre-and Postnuptial Agreements</li></ul>'},
                        { header: 'SECURE THE FUTURE FOR YOU AND YOUR LOVED ONES',
                        paragraphs: '<p>Its vital to have a basic estate plan in place. Estate planning is one of the most important steps any person should take to protect their assets from potential estate taxes and to avoid probate.</p><p>Many individuals assume they dont need an estate plan, but if you own assets, then you do. Estate planning helps you make decisions about the distribution of your wealth that a probate judge would otherwise make for you. Estate plans are not just about preserving your wealth for your beneficiaries. They also help to plan end of life decisions as well as hospice and long term care.</p><ul><li>Prevents court control of assets at incapacity</li><li>Brings all your assets together under one plan</li><li>Reduce or eliminate state estate taxes</li><li>Reduce or eliminate state inheritance taxes</li><li>Protect inheritance</li><li>Protect dependents with special needs</li><li>Protect assets from creditors</li><li>Avoid the need for court supervision of care for minor children, in the event of the untimely death of parents</li><li>Provide funds for educational expenses of children, distributing funds over as many years as desired</li></ul>'},
                        { header: 'AVOID LITIGATION THROUGH THE PREPARATION OF COMPLETE CONTRACTS',
                        paragraphs: '<p>Transactional practice involves researching, preparing and reviewing the documents that bring individuals and companies together. From contracts for large corporate mergers and acquisitions, to the business partnership buy-sell agreements, Harris Law Office drafts and reviews any contracts you need for your business or personal use. A contract is a necessary to create a clear set of expectations, accountability, recourse and protection for your rights and interest.</p><p>Harris Law Office represents individuals and corporations alike with any contract legal issues. We assist in contract drafting and review, the sale and acquisition of business assets, and the entry into and exit from professional practice groups. We guide our clients through both multi-million dollar territory acquisitions and simple commercial or residential leases.</p><ul><li>Joint Venture Agreements</li><li>Buy Sell Agreements</li><li>Sales Contracts</li><li>Partnership Agreements</li><li>Business Purchase Agreements</li><li>Employment Contracts</li><li>Non-compete Agreements</li><li>Shareholder Agreements</li><li>Most Types of Business Contracts</li><li>Stock Purchase Agreements</li></ul></p>'},
                         { header: 'MEDIATION: AN ALTERNATIVE TO LITIGATION',
                        paragraphs: '<p>Mediation, or alternative dispute resolution, is a process of negotiation in which a skilled neutral third party assists in a civil disagreement to reach an acceptable solution to a dispute without going to trial. Fear, anger, and resentment diffuses when each party has the opportunity to express their needs and frustrations to one another in a safe environment.</p><p>With the help of a trained, impartial third party, called a mediator, people in dispute can negotiate a settlement of the conflict instead of litigating it. Mediation is a confidential process; the mediator will not discuss the mediation with anyone outside of the parties involved. The mediator does not decide the outcome of the dispute. The mediator facilitates communication between parties to better understand one anothers side and choose the most satisfactory resolution of the disagreement.</p><p><strong>Mediation has the following advantages:</strong></p><ul><li><strong>Client Satisfaction</strong>&mdash; The other party hears your side and you have input and control over the result.</li><li><strong>Speed &amp; Efficiency</strong>&mdash; Quicker and the financial and emotional costs are typically less than the traditional legal process.</li><li><strong>Confidentiality</strong>&mdash; You are not prejudiced by discussions with the mediator, because the process is usually confidential. Typically the mediator can neither be a witness nor talk to anyone about the case.</li><li><strong>Neutrality and Reasonable &amp; Creative Solutions</strong>&mdash; Parties to a mediated dispute have much more flexibility in creating solutions.</li></ul>'},
                        { header: 'ARBITRATION: RESOLVE DISPUTES OUTSIDE OF THE JUDICIAL SYSTEM MORE QUICKLY.',
                        paragraphs: '<p>Unlike mediation, arbitration gives the arbitrator the authority to render a binding decision. This decision will have the same authority and power as a court order. When parties have a disagreement, the arbitrator will act as a neutral third-party reviewing the case to determine; what action should be taken, and the terms under which the dispute will be settled. The decision of the arbitrator is final, and may be legally binding or non-binding.</p><p>The setting for an arbitration is more formal than mediation. The arbitration will mirror a court hearing, with specific time limits for, each partys presentation of proof and arguments. Arbitration is usually voluntary but sometimes it is required by law. If both sides agree to be bound by the arbitrator&rsquo;s decision, it becomes a binding arbitration.</p><p><strong>Types of Cases Harris Law Office Can Arbitrate or Mediate:</strong></p><ul><li>Divorce and separation</li><li>Custody agreements</li><li>Pre-nuptial and post-nuptial agreements</li><li>Personal injury</li><li>Breach of contract</li></ul>'}
                        ],
                    
                    third: [
                    {
                        header: 'FREE LEGAL CONSULTATION',
                        paragraphs: '<p>Harris Law Firm offers a free 30 minute consultation. No matter what legal issue you face or questions you have, we welcome the opportunity to help you with your legal issues. We will review the facts of your case and recommend the best action to take.</p><p><strong><i>Call today or use the <a href="#/contact/" onClick="reloadPage()">contact form</a> to schedule an appointment.</i></strong></p><br><br><div class="consultation-promo" style="background-image: url(assets/consultation5.jpg);"><h2>CALL TODAY</h2><hr class="hr-blue" style="margin:0 auto;float:none !important;"><h1>702-723-9800</h1><h2 style="line-height:1.3em;">FOR A FREE 30 MINUTE<br>CONSULTATION</h2></div>'
                    }
                    ],
                    
                    fourth: [
                        {
                        header: 'Contact Harris Law Office',
                        paragraphs: '<p><strong>Phone:</strong> 702-723-9800</p><p><strong>Address:</strong> 4730 South Fort Apache Road, Suite 300</p><p>Las Vegas, Nevada 89147</p><br><p><strong>Harris Law Office can provide you with the personalized legal solutions that you need and the supportive assistance that you deserve.</p><p><i>Se habla Espaol.</i></p></strong>'
                    }
                        ],
                    
                }
            }
        },
        components: {
            'contact-form': ContactForm,
        }
    }
    
</script>

<style>
    
    button{
