        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        padding-top: 5em;
        padding-bottom: 5em;
        margin-top: 60px;
    }
    
    .consultation-promo h1{
        font-size: 4em;
        color: #0f88ad;
    }
    
    .form-control{
        border-radius: 0;
    }
    
    h3{
        text-transform: uppercase;
    }
    
