        content: " ";
    color: #0f88ad;
    position: absolute;
    margin-left: -5px;
    margin-top: -2px;
    }
    
    
    .hr-blue{
    border-color: #0f88ad;
    width: 18px;
    margin-top: 10px;
    margin-bottom: 20px;
    text-align: left;
    float: left !important;
    clear: both !important;
    }
    
    .main-content h3{
    color: #0f88ad;
    margin-bottom: 0;
    }
    
    .specialties{
        margin-bottom: 0.5rem !important;
    }
    
    @media (min-width: 577px){
    
    #contact-form .form-group{
        padding-right: 0;
        padding-left: 0;
    }
    
    #contact-form .form-group.submit{
        padding-left: 0 !important;
    }    
    
    #contact-form .form-group:nth-child(2), #contact-form .form-group:nth-child(4){
        padding-left: 10px;
    }
        
    }
    
    @media (max-width: 991px){
        
    .main-content{
        padding-top: 120px;
        padding-bottom: 110px;
        }
        
        .main-content li:before{
            font-size: 28px;
        }
        
        #contactForm{
            margin-top: 60px;
        }
        
        .hr-blue{
            width: 38px;
            margin-top: 30px;
            margin-bottom: 30px;
        }
        
        .main-content li{
            font-size: 42px !important;
            line-height: 64px;
            padding-left: 2em;
            text-indent: -1em;
    }
        
        .main-content h3{
            font-size: 62px;
        }
        
        .consultation-promo h1{
        font-size: 2.5em;
    }
        
        .consultation-promo{
            padding-bottom: 0;
            padding-top: 0;
            margin-top: 0;
            background-image: none !important;
        }
        
        .consultation-promo h2{
            font-size: 52px;
        }
        
        .form-control, button.btn{
            min-height: 64px;
            font-size: 34px;
        }
        
        .serving-clients{
        max-width: 100%;
    }
        
        #contact-form .form-group:nth-child(2), #contact-form .form-group:nth-child(4){
            padding-left: 0;
        }
        
        #contact-form{
            margin-top: 40px;
        }
    }
    
</style>


