<template>
  
   <transition name="fade">
   <div class="blue-quote-wrap col-xs-12 col-md-3 col-lg-4 pull-right text-center">
    <div v-if="currentView == 1" class="blue-quote " :style="{ 'background-image' : 'url(assets/' + quotes.first[currentContent - 1].image + ')'}">
        <p >{{ quotes.first[currentContent - 1].quote }}</p>
    </div>
    <div v-else-if="currentView == 2" class="blue-quote" :style="{ 'background-image' : 'url(assets/' +  quotes.second[currentContent - 1].image + ')'}">
        <p>{{ quotes.second[currentContent - 1].quote }}</p>
    </div>
    <div v-else-if="currentView == 3" class="blue-quote"  :style="{ 'background-image' : 'url(assets/' + quotes.third[currentContent - 1].image + ')'}">
        <p>{{ quotes.third[currentContent - 1].quote }}</p>
    </div>
    <div v-else="currentView == 4" class="blue-quote" :style="{ 'background-image' : 'url(assets/' + quotes.fourth[currentContent - 1].image + ')'}">
        <p>{{ quotes.fourth[currentContent - 1].quote }}</p>
    </div>
    </div>
    </transition>
</template>

<script>
    export default {
        props: ['currentView', 'tempIndex'],
        methods: {
            quoteFadeIn: function(){
                this.currentContent  = this.tempIndex;
                TweenLite.to($('.blue-quote'), 0.8, {
                opacity: 1,
                ease: Power1.easeInOut
            });
            }
        },
        watch: {
          tempIndex: function() {
              
              TweenLite.to($('.blue-quote'), 0.8, {
                opacity: 0.01,
                ease: Power1.easeInOut,
                onComplete: this.quoteFadeIn
            });
          }  
        },
        data () {
            return {
                currentContent: '1',
                quotes: {
                    first: [ // use imgs with opacity
                        { image: 'Kurt-office-blue.jpg',
                        quote: 'Kurt has been appointed Mediator for the State of Nevada Foreclosure Mediation Program, holds positions as Pro Tem Judge for the Las Vegas Justice Court, and is a Clark County Court appointed Arbitrator.'}
                        ],
                    second: [
                        { image: 'family-blue.jpg',
                        quote: 'Family law cases are often emotionally charged, and create frustration or despair on what your future may hold. We understand this time in your life may be stressful, thats why we want to be your trusted representation and strong, knowledgeable counsel.'},
                        { image: 'estate-planning-blue.jpg',
                        quote: 'We can draft a custom estate plan with any provisions that you desire. Harris Law Office is dedicated to providing the best service and legal counsel to individuals looking for an estate plan.'},
                        { image: 'transactional-blue.jpg',
                        quote: 'We assist in contract drafting and review, the sale and acquisition of business assets, and the entry into and exit from professional practice groups. We guide our clients through both multi-million dollar territory acquisitions and simple commercial or residential leases.'},
                        { image: 'mediation-blue.jpg',
                        quote: 'Why use mediation? Its quicker, cheaper, and, most importantly, it allows the parties to be in control of the outcome.'},
                        { image: 'arbitration-blue.jpg',
                        quote: 'Whether your need is mediation, arbitration or any other dispute resolution, our experienced attorney will work diligently towards a reasonable solution for the most complex legal disputes.'}
                        ],
                    third: [ // use imgs with opacity
                        { image: 'consultation-blue.jpg',
                        quote: 'We value your business and treat all potential clients with care. We can help determine the best course of action for your specific situation.'}
                        ],
                    fourth: [ // use imgs with opacity
                        { image: 'contact-blue.jpg',
                        quote: 'Harris Law Office can provide you with the personalized legal solutions that you need and the supportive assistance that you deserve.'}
                        ]
                    
                }
            }
        },
        components: {
        }
    }
</script>

<style>
    
    .blue-quote-wrap{
        background-color: #0f88ad;
        padding: 0;
    }
    
    .blue-quote{
        font-style: italic;
        background-size: cover;
        background-repeat: no-repeat;
        height: 400px;
        z-index: 40;
        opacity: 1;
    }
    
    .blue-quote p{
    color: #fff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 80px;
    right: 80px;
    line-height: 28px;
    font-size: 18px;
    }
    
    .fade-enter-active, .fade-leave-active {
  transition: opacity 0.25s ease-out;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
    
    .blue-quote{
        background-color: #3b9dba;
    }

    @media (max-width: 991px){
        .blue-quote{
            min-height: 600px;
        }
    }
    
</style>


