<template>
   
   <form data-toggle="validator" role="form" method="post" name="contactForm" id="contactForm" @submit="send">
   
   
   <br><div class="row"><div class="form-group col-md-6"><input type="text" v-model="contactForm.fName" id="first_name" name="first_name" placeholder="First Name" required="required" maxlength="50" class="form-control" style="background-image: url(&quot;data:image/png;base64,&quot;); background-repeat: no-repeat; background-attachment: scroll; background-size: 16px 18px; background-position: 98% 50%; cursor: auto;"></div> <div class="form-group col-md-6"><input type="text" v-model="contactForm.lName" id="last_name" name="last_name" maxlength="50" placeholder="Last Name" required="required" class="form-control"></div></div><div class="row"><div class="form-group col-md-6"><div class="input-group"><input v-model="contactForm.email" type="email" id="email" name="email" placeholder="Email Address" maxlength="80" required="required" class="form-control"></div></div><div class="form-group col-md-6"><div class="input-group"><input v-model="contactForm.phone" type="tel" maxlength="30" id="telephone" name="telephone" data-mask="(000) 000-0000" data-minlength="14" placeholder="Phone Number" required="required" class="form-control"></div></div><div class="form-group col-md-12"><textarea v-model="contactForm.text" name="comments" id="comments"maxlength="1000" class="form-control" placeholder="Your Message (optional)" rows="4" style="resize:none;"></textarea></div><div class="form-group col-md-12"><button id="submit" type="submit" class="btn col-md-12 btn-primary"><b>Submit</b></button></div></div></form>
    
</template>

<script>
    export default {
        methods: {
            send: function(e) {
                
                e.preventDefault();
                
                $.ajax({
    type: "POST",
    url: "https://mandrillapp.com/api/1.0/messages/send.json",
    data: {
    "key": "4Y0-WoQFNIIZw1NqxQYvfw",
    "message": {
        "text":this.contactForm.text,
        "subject": "Harris Law TEST Subj",
        "from_email":'info@nchinc.com',
        "from_name": 'Cort Christie',
        "to": [
            {
                "email": this.contactForm.email,
                "name": this.contactForm.fName + ' ' + this.contactForm.lName,
                "type": "to"
            }
        ]
    },
    "async": true
    },
                    error: function(xhr, status, error) {

      var err = eval("(" + xhr.responseText + ")");
      alert(err.Message);
    },
           success: function ( ){
 alert ( " Done ! " );
}         
});
                
alert("Gracias por ponerte en contacto con nosotros.")

}
        },
        data () {
            return {
                contactForm: {
                    fName: '',
                    lName: '',
                    email: '',
                    phone: '',
                    text: ''
            }
            }
        },
        components: {
        }
    }
</script>

<style>
    
</style>


