<template>
  
   <div class="lines-wrap">
       <div class="line line-1"></div>
       <div class="line line-2"></div>
       <div class="line line-3"></div>
   </div>
    
</template>

<script>
    export default {
        data () {
            return {
            }
        },
        components: {
        }
    }
</script>

<style>
    
    
    .line{
        position: absolute;
        width: 1px;
        height: 100%;
        top: 0;
        margin-left: -1px;
        background: rgba(0,0,0,0.15);
        z-index: 14;
        /* switch between z-index 15 n 20 */
    }
    
    .main-lines .line.line-1{
        left: 25%;
        height: 1260px;
        z-index: 16;
    }
    
    .main-lines .line.line-2{
        left: 50%;
        height: 390px;
        z-index: 16;
    }
    
    .main-lines .line.line-3{
        left: 75%;
        height: 1260px;
    }
    
    .cover-lines .line{
        height: 1820px;
    }
    
    .cover-lines .line.line-1{
        left: 25%;
        z-index: 10;
    }
    
    .cover-lines .line.line-2{
        left: 50%;
        z-index: 16;
    }
    
    .cover-lines .line.line-3{
        left: 75%;
        z-index: 10;
    }
    
    
</style>


