<template>
   <div id="main-view">
    <main-nav @permitTween="permitTween" :coverToNavPrevTween="coverToNavPrevTween" @emitPrevView="prevView" @emitNextView="nextView" @emitClick="navItemClick($event, i)" :currentView="index"></main-nav>
    <main-body @clickToContact="clickToContact" :currentView="currentView"></main-body>
    
    </div>
</template>

<script>
    import MainNav from './Nav.vue'
    import MainBody from './Main-body.vue'
    
    export default {
        props: ['coverSelectView', 'view', 'viewChanges', 'coverToNavPrevTween'],
        data () {
            return {
                currentView: '1',
                index : '1',
                i: ''
            }
        },
        components: {
            'main-nav': MainNav,
            'main-body': MainBody
        },
        methods: {
            
            assignCurrentView: function(x){ 
                this.currentView = ( x );
            },
            nextView: function(){
                this.index++
                this.$emit('coverToNavBottom');
            },
            prevView: function(){
                this.index--
                this.$emit('coverToNavBottom');
            },
            navItemClick: function(i) {
                this.index = i + 1;
                this.$emit('coverToNavBottom');
            },
            permitTween: function(){
                this.$emit('permitTween');
            },
            clickToContact: function() {
                this.navItemClick(3);
            }

        },
        watch: { 
      	     coverSelectView: function(newVal, oldVal) {
                 //this.imgTransition(newVal, oldVal);
                 console.log('Prop changed: coverSelectView:', newVal, ' | was: ', oldVal)
                 
                 this.index = newVal;
                 
                 //$('li.inactive').css('top', '0px');
                 //$('li.active').css('top', '315px');
                 $('.cover-item').css('display', 'none');
            },
            viewChanges: function(){
                this.assignCurrentView(this.index);
                
               if (this.index == 1) {
                     window.location.href = '#/kurt-harris/';
                 } else if (this.index == 2) {
                     window.location.href = '#/practice/';
                 } else if (this.index == 3) {
                     window.location.href = '#/consultation/';
                 } else {
                     window.location.href = '#/contact/';
                 }
                 
            }
    }
    }
    
    /*
      function kurtHarris(e) {
        console.log('switched to Kurt Harris!');
          e.preventDefault()
      }
    
  function practice(e) {
        console.log('switched to practice!');
      e.preventDefault()
      }
    
  function consultation(e) {
        console.log('switched to consultation!');
      e.preventDefault()
      }
    
  function contact(e) {
        console.log('switched to contact!');
      e.preventDefault()
      }
      */
    
</script>




