<template>
    <div class="form-wrap col-md-3 col-xs-12 pull-left" style="background-image: url(assets/harris-cover-b4c4ca-softlight-20.jpg);">
       <p>{{ sideForm.email }}</p>
       <p>{{ sideForm.fName }}</p>
       <p>{{ sideForm.lName }}</p>
       <p>{{ sideForm.phone }}</p>
        
        <form data-toggle="validator" role="form" @submit="send" method="post" enctype="text/plain" id="side-form">
        <h3 class="text-center">FREE CONSULTATION</h3>
<input type="hidden" id="page" name="page" value="<?php echo $current_page; ?>" />
            
<div class="form-group">

    <input v-model="sideForm.fName" type="text" class="form-control" id="first_name" name="first_name"  placeholder="First Name" required>
    
  </div>
  
    <div class="form-group ">
     
       <input v-model="sideForm.lName" type="text" class="form-control" id="last_name" name="last_name" placeholder="Last Name" required>
       
      </div>    

<div class="form-group">
    <div class="input-group">

    <input v-model="sideForm.email" type="email" class="form-control" id="email" name="email" placeholder="Email Address" required>
    
    </div>
  </div>

<div class="form-group">
    <div class="input-group">

    <input v-model="sideForm.phone" type="tel" class="form-control" id="phone" name="phone" data-mask="(000) 000-0000" data-minlength="14" placeholder="Phone Number" required>
    </div>
  </div>
  
    <div class="form-group">
    <button id="submit" type="submit" class="btn col-xs-12 btn-default"><b>Get Started</b></button>
  </div>
</form>
        
    </div>
</template>

<script>
    export default {
        data () {
            return {
                sideForm: {
                    fName: '',
                    lName: '',
                    email: '',
                    phone: '',
                    text: 'lorem ipsum gsergaregzdrgzdrgzrgzer szdrgzsrgzr xzdrg'
            },
            }
        },
        components: {
        },
        methods: {
            send: function(e) {
                
                e.preventDefault();
                
                $.ajax({
    type: "POST",
    url: "https://mandrillapp.com/api/1.0/messages/send.json",
    data: {
    "key": "4Y0-WoQFNIIZw1NqxQYvfw",
    "message": {
        "text":this.sideForm.text,
        "subject": "Harris Law TEST Subj",
        "from_email":'info@nchinc.com',
        "from_name": 'Cort Christie',
        "to": [
            {
                "email": this.sideForm.email,
                "name": this.sideForm.fName + ' ' + this.sideForm.lName,
                "type": "to"
            }
        ]
    },
    "async": true
    },
                    error: function(xhr, status, error) {

      var err = eval("(" + xhr.responseText + ")");
      alert(err.Message);
    },
           success: function ( ){
 alert ( " Done ! " );
}         
});
                
alert("Gracias por ponerte en contacto con nosotros.")

}
                
            }
        }
    
</script>

<style>
    
    .form-wrap{
        padding: 80px;
        background-color: #0f88ad;
        color: #fff;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    
    @media (min-width: 992px) and (max-width: 1399px){
        
        .form-wrap{
            padding-top: 60px;
            padding-left: 30px;
            padding-right: 30px;
        }
        
        .side-nav{
            padding-left: 30px;
            padding-right: 30px;
        }
          
        .side-nav ul{
            padding-left: 10px;
        }
        
        .blue-quote p{
            left: 40px;
            right: 40px;
        }
        
        .consultation-promo h2{
            font-size: 24px;
        }
        
        .consultation-promo h1{
            font-size: 3em;
        }
        
        .consultation-promo{
            margin-top: 30px;
        }
        
        .main-content{
            padding-left: 40px;
            padding-right: 40px;
        }
    }
    
    .input-group{
        width: 100%;
    }
    
    #submit{
        padding: 10px 0;
    }
    
    @media (max-width: 991px){
        #submit{
        width: 100% !important;
        }
        
        #side-form .form-group{
            margin-bottom: 30px;
        }
    }
    
</style>


