
   <template>
<div class="nav-img-wrap" style="background-image: url(assets/overlay.png);">

  <div class="nav-img" :style="{ backgroundImage: 'url(assets/' + imgList[currentView - 1 ] + ')' }"></div>
  
  <div class="nav-img"></div>
  
  <!-- 
  <div class="nav-img" :style="{ backgroundImage: 'url(assets/' + imgList[currentView - 1 ] + ')' }"></div>
  <div v-if="currentView == 2" style="width:200px;height:200px;background-color:blue;"></div>-->
   
</div>
</template>

<script>
    
    export default{
        props: ['currentView'],
        data(){
            return{
                imgList: ['lawyers5.jpg', 'skyline8.jpg', 'consultation.jpg', 'justice.jpg']
            }
        },
        methods: {
            imgTransition: function(newVal, oldVal) {
                
                $('.nav-img-wrap .nav-img:nth-child(2)').css('background-image', 'url(assets/' + this.imgList[oldVal -1] + ')');
                // image in back gets old image
                
                $('.nav-img-wrap .nav-img:nth-child(2)').css('margin-top', '0');
                
                 
                // new image animated in with height to 380 from 0
                TweenLite.fromTo($('.nav-img-wrap .nav-img:first-child'), 1.2, {top:"-340px"}, {top:0, ease: Power3.easeOut});
                    // removed height transition - imgs are just showing without. add back in
            }, 
            
          /*  imgTransition: function(newVal, oldVal) {
                // image in back gets old image
                $('.nav-img-wrap .nav-img:first-child()').css('background-image', 'url(assets/' + this.imgList[oldVal -1] + ')');
                 
                // new image animated in with height to 380 from 0
                 TweenLite.fromTo($('.nav-img-wrap .nav-img)'), 0.85, {top:"340px"}, {top:0, ease: Power3.easeOut});

            }*/
            
        },
        
        watch: { 
      	     currentView: function(newVal, oldVal) { // watch for change in currentView
                 this.imgTransition(newVal, oldVal); // call transition method
                 console.log('Prop changed: currentView:', newVal, ' | was: ', oldVal)
            }
    }
    }
    
    
</script>

<style>
    
    .nav-img-wrap{
         background-color: #eaeaea;
        background-position: center 380px;
         margin-top: -50px;
         width: 100%;
         height: 380px;
         z-index:60;
         top: 50px;
    }
    
    .nav-img-wrap .nav-img{
        background-repeat: no-repeat;
         background-size: cover;
         width: 100%;
         height: 100%;
        position: absolute;
        height: 340px;
        top: 0;
    }
    
    .nav-img-wrap .nav-img:first-child{
        z-index: 10;
    }
    
    .nav-img-wrap .nav-img:nth-child(2){
        margin-top: -380px;
        height: 380px;
        z-index: 5;
    }
    
    @media (max-width: 991px){
        .nav-img-wrap{
            display: none;
        }
    }
    
</style>


