<template>
    <div class="side-nav col-md-3 col-xs-12 ">
        <ul>
            <li @click="emitClick(i)" v-if="currentView == 1" v-for="(link, i) in links.first">{{ link }}</li>
            <li @click="emitClick(i)" v-if="currentView == 2" v-for="(link, i) in links.second">{{ link }}</li>
            <li @click="emitClick(i)" v-if="currentView == 3" v-for="(link, i) in links.third">{{ link }}</li>
            <li @click="emitClick(i)" v-if="currentView == 4" v-for="(link, i) in links.fourth">{{ link }}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        props: ['currentView'],
        data () {
            return {
                links: { 
                    first: ['ATTORNEY AT LAW'/*, 'MEDIATOR & LITIGATOR', 'SPECIALTIES'*/],
                    second: ['DOMESTIC AND FAMILY LAW', 'ESTATE PLANNING', 'TRANSACTIONAL', 'OFFICE MEDIATION', 'ARBITRATION'],
                    third: ['FREE LEGAL CONSULTATION'],
                    fourth: ['GET IN TOUCH']
                       }
            }
        },
        methods:{
            emitClick: function(i){
                this.$emit('emitClick', i);
                console.log('click emitted');
            }
        },
        components: {
        }
    }
</script>

<style >
    .side-nav{
        background-color: #fff;
        height: 400px;
        text-align: right;
        padding: 60px;
        z-index: 10;
    }
    
    .side-nav ul{
        border-top: 1px solid #0f88ad;
    }
    
    .side-nav li{
        text-align: right;
        height: auto;
        padding-bottom: 5px;
    }

    .side-nav li:hover{
        color: #0f88ad;
    }
    
    .side-nav li:first-child{
        margin-top: 8px;
    }
    
    @media (max-width: 991px){
        
        .nav-items{
            margin-top: 0;
        }
        
        .side-nav{
            height: auto;
            padding-top: 15px;
            padding-bottom: 15px;
            background-color: #eaeaea;
        }
        
        .side-nav ul{
            border-top: 0;
        }
        
        .side-nav li{
            text-align: center;
            font-size: 42px;
            padding: 35px;
        }
        
        
    }
</style>


